import Vue from 'vue'
import Vuex from 'vuex'
import {getPosts} from '@/services/postsService'
import {insertPost} from '@/services/postsService'
import {getMessages} from '@/services/messagesService'
import {insertMessage} from '@/services/messagesService'
import {insertUser} from '@/services/userService'
/*import post from './modules/post'
import message from './modules/message'
import keycloak from './modules/keycloak'*/

Vue.use(Vuex)

export default new Vuex.Store({
	state: {
		keycloack:'',
		posts : []
	},
	mutations: {
		login (state, payload){
			state.keycloack = payload
		},
		setPosts (state, posts) {
			state.posts = posts
		}
	},
	actions: {
		fetchData({commit}) {
			return getPosts()
			.then((posts) => {
				console.log('posts from STORE ', posts)
				commit('setPosts', posts)
			})
		},
		fetchMessages ({commit}) {
			return getMessages().then((posts) => {
				console.log('posts from STORE ', posts)
				commit('setPosts', posts)
			})
		},
		insert ({commit},post) {
			return insertPost(post).then((response) => {
				console.log(response)
			})
		},
	insertmessage ({commit},message) {
		return insertMessage(message).then((response) => {
			consle.log(response)
		})
	},

	insertuser ({commit},user) {
		return insertUser(user).then((response) => {
			consle.log(response)
		})
	},
},


   /* modules: {
        post,
        message,
        keycloak
    }*/
})