import { fetchWithToken } from '@/utils/Keycloak'
//import config from '@/assets/config.json'

const backend = 'https://post.twilos.codes'

export function getPosts () {
  const url = `${backend}/posts`
  console.log('pouet2')
  return fetchWithToken(url, { 
    method: 'GET',
    mode: 'cors',
    credentials: 'include',
    headers: {
        //'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json',
      },
    //credentials: 'same-origin',
     })
    .then((response) => {
      //console.log('pouet3')
      //console.log('response', response)
      //console.log('pouet4')
      return response.json()
     // .then(data => console.log(data));
      })
    .then(data => {
      //console.log('mes posts : ', data)
      return data
    })
}

export function insertPost (post) {
    const url = `${backend}/addpost`
    return fetchWithToken(url, {
    method: 'POST',
    body: JSON.stringify(
      {
        "contenu":  post.contenu,
        "expediteur":   2,
        "media":    "poue",
        "date_envoi":   "2021-04-04 09:28:07",
        "nbr_like": 2
      }
        ),
    mode: 'cors',
    credentials: 'include',
    headers: {
        //'Access-Control-Allow-Origin': '*',
        'Content-Type': 'application/json',
      },
    //credentials: 'same-origin',
     })
    .then((response) => {
        console.log('response', response)
        //response.json()
      })
}

export function update (id) {
const params = new URLSearchParams()
params.append('id', id)
const url = `${backend}/message/${params.toString()}`
return fetchWithToken(url, { method: 'PUT' })
    .then(response => response.json())
    .then(result => {
        return result
    })
}

export function suppr (id) {
const params = new URLSearchParams()
params.append('id', id)
const url = `${backend}/message/${params.toString()}`
return fetchWithToken(url, { method: 'DELETE' })
    .then(response => response.json())
    .then(result => {
        return result
    })
}